@import url(https://fonts.googleapis.com/css2?family=Limelight&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.Register{
   height: 100vh;
   -webkit-justify-content: center;
           justify-content: center;
   border: red;
   width:50%;

}
.error{
  color: White;
  text-align: center;
  margin-top: 30px;
}
.reg{
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  margin-top: 20px;
}
.banner{
  height: 200px;
  width: 300px;
}
.highlighter{
  text-align: center;
  font-size: 50px;
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.name{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;

}
.btnh:hover{
  border-radius: 30px;
  color: skyblue;
  background-color: white;  
}
.btnh{
  background-color: skyblue;
  height: 50px;
  border-radius: 55px;
  font-weight: 700;
  color: white;
  font-size: 20px;
}
.quiz{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
.subname{
  border: 3px solid orange;
  border-radius: 50px;
  width:50%;
  text-align: center;
  font-weight: 600;
  margin-top: 20px;
  box-shadow: 5px 5px 5px orange;
  background-color: paleturquoise;
}
.question{
  border:15px solid lawngreen;
  -webkit-align-items: center;
          align-items: center;
  width: 90%;
  
  border-radius: 10px;
  height: inherit;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 20px;
}
.qustioname{
  width:95%;
  min-height: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  padding: 10px;
}
.option{
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-flex:1 1;
          flex:1 1;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 10px;

}
.selectOption{
  width:45%;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  height: 50px;
  margin: 10px;
  background-color: ivory;
  color: black;
  font-weight: 600;
}
.selectOption:hover{
  background-color: black;
  color: aliceblue;
  text-transform: 200ms;
  border-radius: 20px;
}
@media (max-width: 614px){
  .selectOption{
    width: 100%;
  }
}
.select{
 background-color:green;
}
.wrong{
  background-color:red
}
.result {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  height: 60vh;
  text-align: center;
}
.animated-box {
 display: -webkit-flex;
 display: flex;
 -webkit-align-items: center;
         align-items: center;
 -webkit-flex-direction: column;
         flex-direction: column;
 text-align: center;
  
  -webkit-animation:fontbulger 2s infinite;
  animation:fontbulger 2s infinite;
}

@-webkit-keyframes fontbulger  {
  0%, 100% {
     font-size:16px;
  }
  
  50% {
     font-size:21px;
  }

}

@keyframes fontbulger  {
  0%, 100% {
     font-size:16px;
  }
  
  50% {
     font-size:21px;
  }

}
.nq{
  display: -webkit-flex;
  display: flex;
  width:70%;
}
.hr{
    background-color : grey;
    margin: 30px;
    width:96%;
    border-radius: 2px;
}

.header{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    background-color: black;
    border-radius: 30px;
}
.title{
    font-family: 'Limelight', cursive ;
    font-size: 10vh;
    color: white;
}
* { 
  margin :0;
  padding: 0;
  scrollbar-width: thin;
  box-sizing: border-box ;
}
*::-webkit-scrollbar{
  width: 5px;
  background-color: gray;
}
.App{
  min-height: 100vh;
  border: 8px solid red;
  border-radius: 20px;
  margin: 5px;
  padding: 5px;
  background: linear-gradient(rgb(240, 239, 235), rgb(8, 8, 8));

}
a{
  text-decoration: inherit;
  color: inherit;
   
}
